const RESERVE_SIT = "RESERVE_SIT";
const RESET_SITS = "RESET_SITS";
const UPDATE_STATE = "UPDATE_STATE";
const RESET_ORDER = "RESET_ORDER";

const InitalState = {

    reservedSits: {
        numbers : [],
        sits: {
            Loge: 0,
            Parkett: 0,
        },
        price: 0,
    },
    sits : [
        {number: 0, reserved: false, type: "Loge", booked: false},
        {number: 1, reserved: false, type: "Loge", booked: false},
        {number: 2, reserved: false, type: "Loge", booked: false},
        {number: 3, reserved: false, type: "Loge", booked: false},
        {number: 4, reserved: false, type: "Loge", booked: false},
        {number: 5, reserved: false, type: "Loge", booked: false},
        {number: 6, reserved: false, type: "Loge", booked: false},
        {number: 7, reserved: false, type: "Loge", booked: false},
        {number: 8, reserved: false, type: "Loge", booked: false},
        {number: 9, reserved: false, type: "Loge", booked: false},
        {number: 10, reserved: false, type: "Loge", booked: false},
        {number: 11, reserved: false, type: "Loge", booked: false},
        {number: 12, reserved: false, type: "Parkett", booked: false},
        {number: 13, reserved: false, type: "Parkett", booked: false},
        {number: 14, reserved: false, type: "Parkett", booked: false},
        {number: 15, reserved: false, type: "Parkett", booked: false},
        {number: 16, reserved: false, type: "Parkett", booked: false},
        {number: 17, reserved: false, type: "Parkett", booked: false},
        {number: 18, reserved: false, type: "Parkett", booked: false},
        {number: 19, reserved: false, type: "Parkett", booked: false},
        {number: 20, reserved: false, type: "Parkett", booked: false},
        {number: 21, reserved: false, type: "Parkett", booked: false},
        {number: 22, reserved: false, type: "Parkett", booked: false},
        {number: 23, reserved: false, type: "Parkett", booked: false},
    ]
};

const DisplayReducer = (state = InitalState, action) => {
    switch(action.type){
        case RESERVE_SIT:
            const newState = Object.assign({}, state);
            const isReserved = newState.sits[Number(action.sit)].reserved;
            newState.sits[Number(action.sit)].reserved = !state.sits[Number(action.sit)].reserved; // reserved true/false ändern
                if(!isReserved) {
                    newState.reservedSits.numbers.push(action.sit);
                    if (newState.sits[Number(action.sit)].type === "Parkett") {          // in den reservedSits state sits hinzufügen
                        newState.reservedSits.sits.Parkett += 1;
                        newState.reservedSits.price += 1000;
                    } else {
                        newState.reservedSits.sits.Loge += 1;
                        newState.reservedSits.price += 5000;
                    }
                } else {
                    const deletenumber = action.sit;
                    const newnumbers = newState.reservedSits.numbers.filter((number) => {
                        return number !== deletenumber;
                    })
                    newState.reservedSits.numbers = newnumbers;
                    if (newState.sits[Number(action.sit)].type === "Parkett") {          // in den reservedSits state sits hinzufügen
                        newState.reservedSits.sits.Parkett += -1;
                        newState.reservedSits.price += -1000;
                    } else {
                        newState.reservedSits.sits.Loge += -1;
                        newState.reservedSits.price += -5000;
                    }
                }
            return newState;
        case RESET_SITS:
            console.log("@ Display reducer, reseting all sits", state);
            const stateCopy = Object.assign({}, state);
            for (let sit of state.sits){
                sit.reserved = false;
            }
            stateCopy.reservedSits = {
                sits: {
                    Loge: 0,
                    Parkett: 0,
                },
                price: 0,
            }
            return stateCopy;
        case UPDATE_STATE:
            const stateCopy2 = Object.assign({}, state);
            const numbers = action.newState.data[0]?.reservedSits.numbers;
            numbers?.forEach((number) => {
                stateCopy2.sits[number].reserved = true;
                stateCopy2.sits[number].booked = true;
            })
            return stateCopy2;
        case RESET_ORDER:
            const stateCopy3 = Object.assign({}, state);
            console.log("@ displayreducer, reseting order...");
            stateCopy3.reservedSits = {
                numbers : [],
                sits: {
                    Loge: 0,
                    Parkett: 0,
                },
                price: 0,
            }
            return stateCopy3;
        default:
            return state;
    }
}

export const reserveSitActionCreater = (sitNumber) => {
    return {type: RESERVE_SIT, sit: sitNumber}
}

export const resetSitsActionCreater = () => {
    return {type: RESET_SITS}
}

export const updateStateActionCreater = (newState) => {
    return {type: UPDATE_STATE, newState: newState}
}

export const resetOrderActionCreater = () => {
    return {type: RESET_ORDER}
}

export default DisplayReducer;