import s from "./modal.module.css";
import M from "materialize-css";

import {HandleModalActionCreater, UpdateNameActionCreater, UpdateEmailActionCreater} from "../../redux/modal.reducer";

import Modal from "./Modal";
import {connect} from "react-redux";

const mapStateToProps = (state) => {
    return {
        modal: state.Modal,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updatename: (name) => {
            const action = UpdateNameActionCreater(name);
            dispatch(action);
        },
        updateemail: (email) => {
            const action = UpdateEmailActionCreater(email);
            dispatch(action);
        },
        placeOne: () => {
            const action = HandleModalActionCreater();
            dispatch(action);
        }
    }
}

const ModalContainer = connect(mapStateToProps, mapDispatchToProps)(Modal);


export default ModalContainer;