import s from "./modal.module.css";
import M from "materialize-css";

import {getApiResource, updateDashboardState} from "../../utils/network";

const Modal = ({modal, updatename, updateemail, placeOne}) => {

    console.log("modal @ modal", modal.data);

    const placeOrder = async () => {
        console.log("place order, data", modal.data);
        const data = modal.data;

        if(!data.name || !data.email){
            M.toast({html: "Please, enter your email and name"});
        } else{
            await getApiResource(data);             /// create new booking
            await updateDashboardState(data);
            placeOne();
        }
    }

    const updateName = (e) => {
        const name = e.target.value;
        updatename(name);
    }

    const updateEmail = (e) => {
        const email = e.target.value;
        updateemail(email);
    }


    return(
        <div className={s.modal}>
            <h1>Check Out</h1>
            <h3>Ordered Sits:</h3>
            <div className={s.order}>
                <p>Loge: {modal.data.order.sits.Loge}</p>
                <p>Parkett: {modal.data.order.sits.Parkett}</p>
                <p>Price: {modal.data.order.price} $</p>
            </div>
            <div className={s.form}>
                <input onChange={(e) => updateName(e)} value={modal.data.name} type="text" name="name" placeholder="please, enter your name"/>
                <input onChange={(e) => updateEmail(e)} value={modal.data.email} type="text" name="email" placeholder="please, enter your email"/>
                <p>Yes, you will get Spam emails from us and also we sell your data to CIA</p>
            </div>
            <button onClick={placeOrder} className={s.button}>Place Order</button>
        </div>
    )
}

export default Modal;