import {resetAll} from "../utils/network";

const RESET_ALL = "RESET_ALL";
const UPDATE_DASHBOARD_STATE = "UPDATE_DASHBOARD_STATE";
const RELOAD_COMPONENT = "RELOAD_COMPONENT";

const InitalState = {
    reload: false,
    turnover: 0,
    reservedSits: {
        numbers: [],
        sits: {
            Loge: 0,
            Parkett: 0,
        },
        price: 0,
    },

}

const DashboardReducer = (state = InitalState, action) => {
    switch(action.type){
        case RESET_ALL:
            const resetPlaces = async () => {
                await resetAll();
            }
            resetPlaces();
            const resetedState = Object.assign({}, state);
            return resetedState;
        case UPDATE_DASHBOARD_STATE:
            state.turnover = action.newState.data[0]?.turnover;
            state.reservedSits = action.newState.data[0]?.reservedSits;
            return state;
        case RELOAD_COMPONENT:
            console.log("@dashboard reducer, realoading...", action);
            state.reload = !state.reload;
            return {...state};
        default:
            return state;
    }
}


export const resetAllActionCreater = () => {
    return {type: RESET_ALL}
}

export const updateDashboardStateActionCreater = (newState) => {
    return {type: UPDATE_DASHBOARD_STATE, newState: newState}
}

export const reloadActionCreater = () => {
    return {type: RELOAD_COMPONENT}
}


export default DashboardReducer;