import s from "./dashboard.module.css";
import 'antd/dist/antd.css';
import React, {useEffect, useState} from 'react';
import {useNavigate} from "react-router-dom";
import {updateStateActionCreater} from "../../redux/display.reducer";
import {Button, Modal, Switch} from 'antd';
import {getDashboardState} from "../../utils/network";
import {
    reloadActionCreater,
    resetAllActionCreater,
    updateDashboardStateActionCreater
} from "../../redux/dashboard.reducer";


const Dashboard = ({dashboard, dispatch}) => {

    {/*reload state*/}
    const [reload, setReload] = useState(false);

    useEffect(() => {
        console.log("reload has changed", dashboard.reload);
    }, [dashboard.reload]);
    {/*reload state*/}

    {/*Modal and Button*/}
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [switchOne, setSwtitchOne] = useState(false);
    const [switchTwo, setSwitchTwo] = useState(false);
    const [switchThree, setSwitchThree] = useState(false);

    const onSwitchOne = (checked) => {
            setSwtitchOne(checked);
    };

    const onSwitchTwo = (checked) => {
            setSwitchTwo(checked);
    };

    const onSwitchThree = (checked) => {
            setSwitchThree(checked);
    }

    const showModal = () => {
        setIsModalOpen(true);
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };
    {/*Modal and Button*/}

    {/*Getting Dashboard Data from Database*/}

    /*useEffect(() => {
        const getDashboard = async () => {
            const dashBoardState = await getDashboardState();
            const action = updateStateActionCreater(dashBoardState);
            const action1 =  updateDashboardStateActionCreater(dashBoardState);
            dispatch(action);
            dispatch(action1);
        }
        getDashboard();

    }, [])*/


    {/*Getting Dashboard Data from Database*/}
    const navigate = useNavigate();

    const resetAllPlaces = async () => {
        const action = resetAllActionCreater();
        console.log("@ dashboard, reset allplaces action", action);
        dispatch(action);
        /*const dashBoardState = await getDashboardState();
        console.log("dashboardstate @ getDashboard", dashBoardState.data[0]);
        const action2 = updateStateActionCreater(dashBoardState);
        const action3 =  updateDashboardStateActionCreater(dashBoardState);
        dispatch(action2);
        dispatch(action3);*/
        const action1 = reloadActionCreater()
        dispatch(action1);
        setReload(!reload);
        handleCancel();
    }

    return(
        <div className={s.dashboard}>
            <h1>This is the Mater Board</h1>
            <div className={s.dashboard_display}>
                <div>Freie Plätze...{dashboard.reservedSits? 24 - Number(dashboard.reservedSits?.numbers.length): 24}</div>
                <div>Umsatz....{dashboard.turnover? dashboard.turnover + " Bucks" : "Es ist Obst im Haus"}</div>
            </div>
            <button onClick={showModal} className={s.button}>
                <p>Selbstzerstörung</p>
                <p>(reset places)</p>
            </button>
            <Modal title="Der Geheime Geheimknopf" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
                <div className={s.modal_content}>
                    <div className={s.switchers}>
                        <Switch onChange={onSwitchOne}/>
                        <Switch onChange={onSwitchTwo}/>
                        <Switch onChange={onSwitchThree}/>
                    </div>
                    <div>
                        {(!switchOne || !switchTwo || !switchThree) && <Button style={{backgroundColor: "red", fontWeight: "700"}} disabled={true} type="danger">RESET</Button>}
                        {(switchOne && switchTwo && switchThree) && <Button onClick={resetAllPlaces} style={{backgroundColor: "red", fontWeight: "700"}} disabled={false} type="danger">RESET</Button>}
                    </div>
                </div>
            </Modal>
        </div>
    )

}

export default Dashboard;