import s from "./display.module.css";
import Place from "./Place/Place";
import {v4 as uuidv4} from "uuid";
import Modal from "../Modal/Modal";
import ModalContainer from "../Modal/ModalContainer";

const Display = ({modal, display, reserveSit}) => {
    console.log("@ Display, display", display);
    return(
        <div className={s.display_container}>
            <div className={s.display}>
                {display.sits.map( (sit) => {
                    return(
                        <Place reserveSit={reserveSit} key={uuidv4()} number={sit.number} reserved={sit.reserved} booked={sit.booked} type={sit.type}/>
                    )
                } )}
            </div>
            {modal.opened && <ModalContainer />}
            {modal.opened && <div className={s.modal_overlay}>

            </div>}
        </div>

    )
}

export default Display;