const HANDLE_MODAL = "HANDLE_MODAL";
const UPDATE_NAME = "UPDATE_NAME";
const UPDATE_EMAIL = "UPDATE_EMAIL";

const InitalState = {

    opened: false,

    data: {
        name: "",
        email: "",
        order: {
            numbers : [],
            sits: {
                Loge: 0,
                Parkett: 0,
            },
            price: 0,
        }
    }
};

const DisplayReducer = (state = InitalState, action) => {

    switch(action.type){
        case HANDLE_MODAL:
            const stateCopy = Object.assign({}, state);
            stateCopy.opened = !state.opened;
            stateCopy.data.order = action.reservedSits;
            return stateCopy;
        case UPDATE_NAME:
            console.log("update name", state);
            const stateCopy2 = Object.assign({}, state);
            stateCopy2.data.name = action.name;
            return stateCopy2;
        case UPDATE_EMAIL:
            console.log("update email", state);
            const stateCopy3 = Object.assign({}, state);
            stateCopy3.data.email = action.email;
            return stateCopy3;
        default:
            return state;
    }
}


export const HandleModalActionCreater = (reservedSits) => {
    return {type: HANDLE_MODAL, reservedSits : reservedSits}
}

export const UpdateNameActionCreater = (name) => {
    return {type: UPDATE_NAME, name: name}
}

export const UpdateEmailActionCreater = (email) => {
    return {type: UPDATE_EMAIL, email: email}
}

export default DisplayReducer;