import { connect } from "react-redux";
import Display from "./Display";
import {reserveSitActionCreater} from "../../redux/display.reducer";
import {HandleModalActionCreater} from "../../redux/modal.reducer";


const mapStateToProps = (state) => {
    return {
        modal: state.Modal,
        display: state.Display,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        reserveSit: (sitNumber) => {
            const action = reserveSitActionCreater(sitNumber);
            dispatch(action);
        },
    }
}


const DisplayContainer = connect(mapStateToProps, mapDispatchToProps)(Display);

export default DisplayContainer;